import './Payment.scss'
import CredirCard from './images/Credit-card.svg'
import PayPAl from './images/Paypal.svg'
import Cash from './images/cash.svg'
import CheckedIcon from './images/checked.svg'
import { Fragment, useState } from 'react'

const PaymentSidebar = ({btnState,setBtnState}) => {

    const [btnClass, setBtnClass] = useState('');

    return (
        <Fragment>
        <div className={`shape ${btnState ? 'active' : ''}`}></div>
            <div className={`payment-sidebar ${btnState ? 'active' : ''}`}>
                <div className="top-payment">
                    <h2>Payment</h2>
                    <p>3 payment method available</p>
                </div>
                <div className="payment-settings">
                    <h2>Payment Method</h2>
                    <div className="button-wrapper">
                        <button onClick={() => setBtnClass('credit')} className={`type-payment ${btnClass === 'credit' ? 'active' : ' '}`}>
                            <img src={CredirCard} className="icon" />
                            <img src={CheckedIcon} className="checked-icon" />
                        Credit Card
                    </button>
                        <button onClick={() => setBtnClass('paypal')} className={`type-payment ${btnClass === 'paypal' ? 'active' : ' '}`}>
                            <img src={PayPAl} className="icon" />
                            <img src={CheckedIcon} className="checked-icon" />
                        Paypal
                    </button>
                        <button onClick={() => setBtnClass('cash')} className={`type-payment ${btnClass === 'cash' ? 'active' : ' '}`}>
                            <img src={Cash} className="icon" />
                            <img src={CheckedIcon} className="checked-icon" />
                        Cash
                    </button>
                    </div>
                    <div className="line">
                        <label htmlFor="name_input">Cardholder Name</label>
                        <input type="text" id="name_input" placeholder="Levi Ackerman" />
                    </div>
                    <div className="line">
                        <label htmlFor="card_number">Card Number</label>
                        <input type="text" id="card_number" placeholder="2564 1421 0897 1244" />
                    </div>
                    <div className="line">
                        <div className="left">
                            <label htmlFor="date">Expiration Date</label>
                            <input type="text" placeholder="02/2022" id="date" />
                        </div>
                        <div className="right">
                            <label htmlFor="cvv">CVV</label>
                            <input type="password" id="cvv" placeholder="***" />
                        </div>
                    </div>
                </div>
                <div className="line-bottom">
                    <div className="left">
                        <label htmlFor="select">Order Type</label>
                        <select id="select">
                            <option>Dine In</option>
                            <option>To Go</option>
                            <option>Delivery</option>
                        </select>
                    </div>
                    <div className="right">
                        <label htmlFor="number">Order Type</label>
                        <input type="number" id="number" />
                    </div>
                </div>
                <div className="conifrom-line">
                    <button onClick={() => setBtnState(!btnState)} className="cancel">Cancel</button>
                    <button className="submit-payment">Confirm Payment</button>
                </div>

            </div>
        </Fragment>
    )
}

export default PaymentSidebar