import { useState } from 'react';
import DeleteIcon from './Delete.svg'
import './order.scss'
import FirstMeal from './images/1-meal.png'
import SecondMEal from './images/2-meal.png'

const Order = ({setBtnState, btnState   }) => {
    const [btnClass, setBtnClass] = useState('')

    return (
        <section className="order">
            <h3 className="subtitle-otrder">Orders #34562</h3>
            <div className="top_button-group">
                <button onClick={() => setBtnClass('dine')} className={`button_top ${btnClass === 'dine' ? 'active' : ' '}`}>Dine In</button>
                <button onClick={() => setBtnClass('togo')} className={`button_top ${btnClass === 'togo' ? 'active' : ' '}`} >To Go</button>
                <button onClick={() => setBtnClass('del')} className={`button_top ${btnClass === 'del' ? 'active' : ' '}`}>Delivery</button>
            </div>
            <div className="top_line">
                <p>Item</p>
                <span>
                    <p>Oty</p>
                    <p>Price</p>
                </span>
            </div>
            <div className="order-item-wrapper">
                <div className="order-item">
                    <div className="left">
                        <div className="top">
                            <div className="wrapper">
                                <img src={FirstMeal} alt="" />
                                <div className="about-meal">
                                    <p>Spicy seasoned sea...</p>
                                    <span className="price_meal">$2.29</span>
                                </div>
                            </div>
                            <button className="number-meal">
                                2
                            </button>
                        </div>
                        <div className="bottom">
                            <input type="text" placeholder="Order Note..." option="Please, just a little bit spicy only." />
                        </div>
                    </div>
                    <div className="right">
                        <span>$ 4,58</span>
                        <button className="delete-button">
                            <img src={DeleteIcon} alt="" />
                        </button>
                    </div>
                </div>
                <div className="order-item">
                    <div className="left">
                        <div className="top">
                            <div className="wrapper">
                                <img src={SecondMEal} alt="" />
                                <div className="about-meal">
                                    <p>Salted pasta with mu...</p>
                                    <span className="price_meal">$ 2.69</span>
                                </div>
                            </div>
                            <button className="number-meal">
                                1
                            </button>
                        </div>
                        <div className="bottom">
                            <input type="text" placeholder="Order Note..." />
                        </div>
                    </div>
                    <div className="right">
                        <span>$ 2.69</span>
                        <button className="delete-button">
                            <img src={DeleteIcon} alt="" />
                        </button>
                    </div>
                </div>
            </div>
            <div className="bottom_order">
                <div className="line">
                    <p>Discount</p>
                    <span>$ 0</span>
                </div>
                <div className="line">
                    <p>Sub total</p>
                    <span>$ 21.03  </span>
                </div>
                <button onClick={() => setBtnState(!btnState)} className="submit-btn">Continue to Payment</button>
            </div>
        </section>
    )
}

export default Order