import './Sidebar.scss'
import Dish1 from '../HomePage/images/dish-1.png'
import Dish2 from '../HomePage/images/dish-2.png'
import Dish3 from '../HomePage/images/dish-3.png'
import Chart from '../DashboradPage/images/Chart.svg'

const SideBar = () => {
    return (
        <div className="sidebar-right">
            <div className="top-part">
                <div className="line">
                    <h2>Most Ordered</h2>
                    <select>
                        <option selected>Today</option>
                        <option>Tommorow</option>
                        <option>Yesterday</option>
                    </select>
                </div>
                <div className="order-wrapper">
                    <div className="order-line">
                        <img src={Dish1} alt="" />
                        <div className="left">
                            <p>
                                Spicy seasoned seafood noodles
                            </p>
                            <span>
                                200 dishes ordered
                            </span>
                        </div>
                    </div>
                    <div className="order-line">
                        <img src={Dish2} alt="" />
                        <div className="left">
                            <p>
                                Salted pasta with mushroom sauce
                            </p>
                            <span>
                                120 dishes ordered
                            </span>
                        </div>
                    </div>
                    <div className="order-line">
                        <img src={Dish3} alt="" />
                        <div className="left">
                            <p>
                                Beef dumpling in hot and sour soup
                            </p>
                            <span>
                                80 dishes ordered
                            </span>
                        </div>
                    </div>
                </div>
                <button className="view-all">
                    View All
                </button>
            </div>
            <div className="bottom-part">
                <div className="top-side">
                    <h2>Most Type of Order</h2>
                    <button>
                        Today
                    </button>
                </div>
                <div className="bottom-side">
                    <img src={Chart} alt=""/>
                </div>
            </div>
        </div>
    )
}

export default SideBar