import './DishCard.scss'

const DishCard = ({ img, name, price, count }) => {
    return (
        <div className="card">
            <div className="img-wrapper">
                <img src={img} alt="" />
            </div>
            <h2>
                {name}
            </h2>
            <p>
                {price}
            </p>
            <span>
                {count}
            </span>
        </div>
    )
}

export default DishCard