import { Fragment } from 'react'
import './Dashboard.scss'
import FirstLogo from './images/Coin.svg'
import SecondLogo from './images/Bookmark.svg'
import ThirdLogo from './images/Customer.svg'
import ArrowUp from './images/increase.svg'
import ArrowDown from './images/ArrowDown.svg'
import OrderCard from '../../components/OrderCard'
import OrderArr from './OrderArr/OrderArr'


const DashBoardPage = () => {
    return (
        <Fragment>
            <div className="wrapper">
                <div className="top-side">
                    <div className="first">
                        <h1>Dashboard</h1>
                        <p>Tuesday 2 Feb, 2021</p>
                    </div>
                    <div className="second">
                        <ul>
                            <li>
                                <div className="top-side">
                                    <div className="logo-wrapper">
                                        <img src={FirstLogo} />
                                    </div>
                                    <p className="increase">
                                        +32.40%
                                    </p>
                                    <div className="mark-wrapper">
                                        <img src={ArrowUp} alt="" />
                                    </div>
                                </div>
                                <h3>$10,243.00</h3>
                                <p>Total Revenue</p>
                            </li>
                            <li>
                                <div className="top-side">
                                    <div className="logo-wrapper">
                                        <img src={SecondLogo} />
                                    </div>
                                    <p className="decrease">
                                        -12.40%
                                    </p>
                                    <div className="mark-wrapper">
                                        <img src={ArrowDown} alt="" />
                                    </div>
                                </div>
                                <h3>23,456</h3>
                                <p>Total Dish Ordered</p>
                            </li>
                            <li>
                                <div className="top-side">
                                    <div className="logo-wrapper">
                                        <img src={ThirdLogo} />
                                    </div>
                                    <p className="increase">
                                        +2.40%
                                    </p>
                                    <div className="mark-wrapper">
                                        <img src={ArrowUp} alt="" />
                                    </div>
                                </div>
                                <h3>1,234</h3>
                                <p>Total Customer</p>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="bottom-side">
                    <div className="order-table">
                        <div className="order-table-top">
                            <div className="line">
                                <h2>Order Report</h2>
                                <button>Filter Order</button>
                            </div>
                            <div className="line">
                                <h3>Customer</h3>
                                <h3>Menu</h3>
                                <h3>Total Payment</h3>
                                <h3>Status</h3>
                            </div>
                        </div>
                        <div className="order-table-bottom">
                            {
                                OrderArr.map(item => (
                                    <OrderCard
                                        img={item.img}
                                        name={item.name}
                                        meal={item.meal}
                                        price={item.price}
                                        className={item.classNam}
                                        status={item.status}
                                    />
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
export default DashBoardPage