import { Fragment, useState } from 'react'
import './HomePage.scss'
import Logo from './search.svg'
import DishCard from '../../components/DishCard'
import DishArr from './DishArr/DishArr'
import PaymentSidebar from '../Payment'


const HomePage = ({btnState, setBtnState}) => {
    const [btnClass, setBtnClass] = useState('')
    
    return (
        <Fragment>
            <section className="home_page">
                <div className="ajrat">
                    <div className="top-side">
                        <div className="left">
                            <h1>Jaegar Resto</h1>
                            <p>Tuesday, 2 Feb 2021</p>
                        </div>
                        <div className="right">
                            <img src={Logo} a />
                            <input type="text" placeholder="Search for food, coffe, etc.." className="search-input" />
                        </div>
                    </div>
                    <div className="type_dishes">
                        <div className="button_group">
                            <button onClick={() => setBtnClass('hot')} className={`button ${btnClass === 'hot' ? 'active' : ''}`}>Hot Dishes</button>
                            <button onClick={() => setBtnClass('cold')} className={`button ${btnClass === 'cold' ? 'active' : ' '}`}>Cold Dishes</button>
                            <button onClick={() => setBtnClass('soup')} className={`button ${btnClass === 'soup' ? 'active' : ' '}`}>Soup</button>
                            <button onClick={() => setBtnClass('grill')} className={`button ${btnClass === 'grill' ? 'active' : ' '}`}>Grill</button>
                            <button onClick={() => setBtnClass('appe')} className={`button ${btnClass === 'appe' ? 'active' : ' '}`}>Appetizer</button>
                            <button onClick={() => setBtnClass('dessert')} className={`button ${btnClass === 'dessert' ? 'active' : ' '}`}>Dessert</button>
                        </div>
                        <div className="line_group">
                            <span className="line"></span>
                            <span className="first_line"></span>
                        </div>
                    </div>
                </div>
                <div className="dish-menu">
                    <div className="top-menu">
                        <h2>Choose Dishes</h2>
                        <select>
                            <option>Dine In</option>
                            <option >To Go</option>
                            <option >Delivery</option>
                        </select>
                    </div>
                    <div className="card-wrapper">
                        {
                            DishArr.map((item) => (
                                <DishCard
                                    img={item.img}
                                    name={item.name}
                                    price={item.price}
                                    count={item.count}
                                />
                            ))
                        }
                    </div>
                </div>
            </section>
            <PaymentSidebar setBtnState={setBtnState}  btnState={btnState}/>
        </Fragment>
    )
}

export default HomePage