import './SideBar.scss'

import {
    LogoIcon,
    HomePageIcon,
    PercentageIcon,
    DashboardIcon,
    MessageIcon,
    NotificationIcon,
    SettingsIcon,
    LogOutIcon
} from '../../components/Icon'
import { Link } from 'react-router-dom'
import { useState } from 'react'

const Sidebar = () => {

    const [ btnState, setbtnState ] = useState('')

    return (
        <section className="sidebar">
            <Link className="icon">
                <LogoIcon />
            </Link>
            <Link to="/" onClick={() => setbtnState('home')} className={`icon ${btnState === 'home' ? 'active' : ' '}`}>
                <HomePageIcon />
            </Link>
            <Link to="/secondpage" onClick={() => setbtnState('percent')} className={`icon ${btnState === 'percent' ? 'active' : ' '}`}>
                <PercentageIcon />
            </Link>
            <Link to="/dashboard" onClick={() => setbtnState('dash')} className={`icon ${btnState === 'dash' ? 'active' : ' '}`}>
                <DashboardIcon/>
            </Link>
            <Link to="/message" onClick={() => setbtnState('message')} className={`icon ${btnState === 'message' ? 'active' : ' '}`}>
                <MessageIcon />
            </Link>
            <Link to="/notification" onClick={() => setbtnState('notification')} className={`icon ${btnState === 'notification' ? 'active' : ' '}`}>
                <NotificationIcon/>
            </Link>
            <Link to="/settings" onClick={() => setbtnState('settings')} className={`icon ${btnState === 'settings' ? 'active' : ' '}`}>
                <SettingsIcon />
            </Link>
            <Link to="/logout" onClick={() => setbtnState('logout')} className={`icon ${btnState === 'logout' ? 'active' : ' '}`}>
                <LogOutIcon />
            </Link>
        </section>
    )
}

export default Sidebar