import Avatar1 from '../../containers/DashboradPage/images/Avatar1.svg'
import './OrderCard.scss'
const OrderCard = ({img, name, meal, price, status, className }) => {
    return (
        <div className="order-card">
            <div className="left">
                <p className="name">
                    <img src={img} alt="" />
                    <span>
                        {name}
                    </span>
                </p>
                <p className="meal">
                    {meal}
                </p>
                <p className="total-price">
                    {price}
                </p>
            </div>
            <div className="right">
                <p className={className} >
                    {status}
                </p>
            </div>
        </div>
    )
}

export default OrderCard