import { Fragment, useState } from "react"
import {
    AppereanceIcon,
    RestuarntIcon,
    ProductIcon,
    NotifiationIcon,
    SecurityIcon,
    AboutIcon
} from './iconsArr'

import './Settings.scss'
import MealCard from '../../components/MealCard'
import MealArr from './MealArr'

const SettingsPage = () => {
    const [btnClass, setBtnClass] = useState('')

    const [btnStatus, setBtnStatus] = useState('')
    return (
        <Fragment>
            <div className="settings-wrapper">
                <h1>Settings</h1>
                <div className="sides-wrapper">
                    <div className="left-settings">
                        <button onClick={() => setBtnClass('apper')} className={`line ${btnClass === 'apper' ? 'active' : ''}`}>
                            <span>
                                <AppereanceIcon />
                            </span>
                            <span>
                                <h3>Appereance</h3>
                                <p>Dark and Light mode, Font size</p>
                            </span>
                        </button>
                        <button onClick={() => setBtnClass('res')} className={`line ${btnClass === 'res' ? 'active' : ''}`}>
                            <span>
                                <RestuarntIcon />
                            </span>
                            <span>
                                <h3>Your Restaurant</h3>
                                <p>Dark and Light mode, Font size</p>
                            </span>
                        </button>
                        <button onClick={() => setBtnClass('product')} className={`line ${btnClass === 'product' ? 'active' : ''}`}>
                            <span>
                                <ProductIcon />
                            </span>
                            <span>
                                <h3>Products Management</h3>
                                <p>Manage your product, pricing, etc</p>
                            </span>
                        </button>
                        <button onClick={() => setBtnClass('notif')} className={`line ${btnClass === 'notif' ? 'active' : ''}`}>
                            <span>
                                <NotifiationIcon />
                            </span>
                            <span>
                                <h3>Notifications</h3>
                                <p>Customize your notifications</p>
                            </span>
                        </button>
                        <button onClick={() => setBtnClass('sec')} className={`line ${btnClass === 'sec' ? 'active' : ''}`}>
                            <span>
                                <SecurityIcon />
                            </span>
                            <span>
                                <h3>Security</h3>
                                <p>Configure Password, PIN, etc</p>
                            </span>
                        </button>
                        <button onClick={() => setBtnClass('about')} className={`line ${btnClass === 'about' ? 'active' : ''}`}>
                            <span>
                                <AboutIcon />
                            </span>
                            <span>
                                <h3>About Us</h3>
                                <p>Find out more about Posly</p>
                            </span>
                        </button>
                    </div>
                    <div className="right-settings">
                        <div className="top">
                            <div className="title-top">
                                <h2>Products Management</h2>
                                <button>Manage Categories</button>
                            </div>
                            <div className="type-line">
                                <button onClick={() => setBtnClass('hot')} className={`btn ${btnClass === 'hot' ? 'active' : ''}`}>Hot Dishes</button>
                                <button onClick={() => setBtnClass('cold')} className={`btn ${btnClass === 'cold' ? 'active' : ''}`}>Cold Dished</button>
                                <button onClick={() => setBtnClass('soup')} className={`btn ${btnClass === 'soup' ? 'active' : ''}`}>Soup</button>
                                <button onClick={() => setBtnClass('grill')} className={`btn ${btnClass === 'grill' ? 'active' : ''}`}>Grill</button>
                                <button onClick={() => setBtnClass('app')} className={`btn ${btnClass === 'app' ? 'active' : ''}`}>Appetizer</button>
                                <button onClick={() => setBtnClass('dessert')} className={`btn ${btnClass === 'dessert' ? 'active' : ''}`}>Dessert</button>
                            </div>
                            <div className="wrapper-line">
                                <span className="line"></span>
                                <span className="active-line"></span>
                            </div>
                        </div>
                        <div className="bottom">
                            <div className="mealсard-adder">
                                <p>
                                    +
                                </p>
                                <p>
                                    Add new dish
                                </p>
                            </div>
                            {
                                MealArr.map(item => (
                                    <MealCard
                                        name={item.name}
                                        price={item.price}
                                        count={item.count}
                                        img={item.img}
                                    />
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}

export default SettingsPage