import { Fragment } from "react"
import DashboardPage from '../containers/DashboradPage'
import RightSideBar from '../containers/SideBarDash'

const Dashboard = () => {
    return (
        <Fragment>
            <div className="dashboard_container">
                <DashboardPage></DashboardPage>
                <RightSideBar/>
            </div>
        </Fragment>
    )
}

export default Dashboard